body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background: #f7ece1;
  display:flex;
  flex-direction: row;
  width: 100vw;
  height: 100vh;
}
svg {
    height: 100%;
    overflow: hidden;
}

.link {
    stroke:#cac4ce;
    stroke-width: 3px;
}
  
.node text {
    stroke:#242038;
    cursor: pointer;
}
  
.node circle{
    stroke:#8d86c9;
    stroke-width:3px;
    fill:#725ac1;
    cursor: pointer;
    transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.node circle:hover {
    transform: scale(2, 2);
    fill: white;
}
.node circle:active {
    transform: scale(1.5, 1.5);
}
.floating {
  display: flex;
  flex-direction: column;
  text-align: center;
}
